.filter {
  display: flex;
  position: relative;
}

.container {
  cursor: pointer;
  display: flex;
}

.container p {
  user-select: none;
  margin-right: 4px;
  font-weight: 400;
  font-size: 14px;
  line-height: 148%;
  color: #5E7793;
  opacity: 0.87;
}

.container:hover p {
  color: #002CFB;
}

.container svg {
  transition: .3s ease;
}

.container:hover path {
  fill: #002CFB;
}

