.item {
  user-select: none;
  cursor: pointer;
  max-width: 100%;
  list-style: none;
  font-weight: 400;
  font-size: 14px;
  line-height: 28px;
  color: #899CB1;
  padding: 6px 0 6px 21px;
}

.item:hover {
  background: #DEE4FE;
  color: #122945;
}